@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Helvetica Neue", Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@layer base {

  html {
    @apply bg-light-black;
  }
  h1 {
    @apply text-red;
    @apply font-bold;
    @apply text-4xl;
  }
  
  h2 {
    @apply text-teal;
    @apply text-2xl;
    @apply font-semibold;
  }
  
  a {
    @apply text-red;
  }
  
  p {
    @apply text-white;
  }
  
  ul {
    @apply text-white;
  }

}
